import type { ChatMessage } from './types';

export class OllamaClient {
  async chat({ messages, model }: { messages: ChatMessage[]; model?: string }) {
    const response = await this.sendMessage(messages[messages.length - 1].content, model);
    return {
      content: response
    };
  }

  async sendMessage(message: string, model: string = 'deepseek-r1:14b') {
    console.log('Sending message to Ollama...');
    
    try {
      // Get detailed response
      const response = await fetch('http://localhost:11434/api/generate', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          model: model,
          prompt: message,
          // Add parameters to potentially reduce memory usage
          max_tokens: 1000,
          temperature: 0.7,
          stream: false
        })
      });

      // If response isn't ok, get the error details
      if (!response.ok) {
        const errorData = await response.json();
        console.error('Ollama error details:', {
          status: response.status,
          statusText: response.statusText,
          error: errorData
        });
        throw new Error(`Ollama error: ${errorData.error || response.statusText}`);
      }

      const data = await response.json();
      console.log('Successful response:', data);
      return data.response;
    } catch (error) {
      console.error('Detailed connection error:', error);
      throw error;
    }
  }

  // Add a simple test method
  async testConnection(model: string = 'deepseek-r1:14b') {
    try {
      const response = await fetch('http://localhost:11434/api/generate', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify({
          model: model,
          prompt: 'test',
          max_tokens: 10,  // Minimal response for testing
          stream: false
        })
      });

      const data = await response.json();
      console.log('Test response:', data);
      return true;
    } catch (error) {
      console.error('Test failed:', error);
      return false;
    }
  }
}

// Export a default instance
export const ollamaClient = new OllamaClient();
